.alert {
  margin-bottom: 8px;
  border: 1px solid;
  border-radius: 2px;
}

.alert.is-inline {
  display: inline-flex;
}

.alert:empty {
  display: none;
}

.alert-inner {
  display: flex;
  align-items: stretch;
}

.alert-inner.is-banner {
  min-width: 1080px;
  max-width: 1320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}

.alert-icon {
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  border-right: 1px solid;
}

.alert-icon.is-banner {
  width: 16px;
  border-right: none;
}

.alert-content {
  flex: 1 0 0;
  padding: 8px 16px;
  overflow: auto;
  text-align: left;
}

.alert-content a,
.alert-content .button-link {
  border-color: #236a97;
}

.alert-error {
  border-color: #ebccd1;
  background-color: #f2dede;
  color: #862422;
}

.alert-error .alert-icon {
  border-color: #ebccd1;
}

.alert-warning {
  border-color: #faebcc;
  background-color: #fcf8e3;
  color: #6f4f17;
}

.alert-warning .alert-icon {
  border-color: #faebcc;
}

.alert-info {
  border-color: #b1dff3;
  background-color: #d9edf7;
  color: #0e516f;
}

.alert-info .alert-icon {
  border-color: #b1dff3;
}

.alert-success {
  border-color: #d6e9c6;
  background-color: #dff0d8;
  color: #215821;
}

.alert-success .alert-icon {
  border-color: #d6e9c6;
}
